import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import axios from 'axios';

// 
import { zhTranslation, enTranslation } from './translations';

// 
export const resources = {
  en: {
    translation: enTranslation
  },
  zh: {
    translation: zhTranslation
  }
};

// 
const publicPath = process.env.PUBLIC_URL || '';

// 
const savedLanguage = localStorage.getItem('i18nextLng');
const fallbackLng = savedLanguage || 'en';

//  i18next
i18n
  .use(LanguageDetector) // 
  .use(initReactI18next) // i18nreact-i18next
  .init({
    fallbackLng, // 
    lng: savedLanguage || navigator.language || 'en', // 
    debug: process.env.NODE_ENV === 'development', // 
    resources, // 
    interpolation: {
      escapeValue: false, // HTML
    },
    returnNull: false, // null
    returnEmptyString: false, // 
    detection: {
      order: ['localStorage', 'navigator', 'cookie', 'htmlTag'], // localStorage
      caches: ['localStorage', 'cookie'], // 
    },
    react: {
      useSuspense: true,
    },
  });

// IP/
export const detectLanguageByIP = async () => {
  try {
    // 
    if (localStorage.getItem('i18nextLng')) {
      return;
    }
    
    const response = await axios.get('https://ipapi.co/json/');
    const { country_code } = response.data;
    
    // 
    // 
    if (country_code && country_code !== 'CN') {
      await i18n.changeLanguage('en');
    } else {
      await i18n.changeLanguage('zh');
    }
  } catch (error) {
    console.error('IP:', error);
    // 
  }
};

// 
i18n.on('languageChanged', (lng) => {
  document.documentElement.setAttribute('lang', lng);
  document.documentElement.setAttribute('dir', lng === 'ar' ? 'rtl' : 'ltr');
  localStorage.setItem('i18nextLng', lng);
  console.log(`: ${lng}`);
});

// 
if (savedLanguage) {
  i18n.changeLanguage(savedLanguage);
} else if (!i18n.language || i18n.language === 'dev') {
  i18n.changeLanguage('en'); // 
}

// 
console.log(':', i18n.language);
console.log(':', i18n.isInitialized);
console.log(':', i18n.t('app.title'));

// i18n
export default i18n; 