import React, { useState, ChangeEvent } from 'react';
import { Form, Button, Alert } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';

interface SequenceInputProps {
    sequence?: string;
  onSequenceChange?: (value: string) => void;
  file?: File | null;
  onFileChange?: (file: File | null) => void;
  onSubmit?: (sequence: string, file: File | null) => void;
  isLoading?: boolean;
  includeSubstrate?: boolean;
  placeholder?: string;
  sequenceType?: 'protein' | 'DNA' | 'RNA';
}

const SequenceInput: React.FC<SequenceInputProps> = ({ 
  sequence: externalSequence,
  onSequenceChange: externalSequenceChange,
  file: externalFile,
  onFileChange: externalFileChange,
  onSubmit, 
  isLoading = false,
  includeSubstrate = false,
  placeholder,
  sequenceType = "protein"
}) => {
  const { t } = useTranslation();
  
  // 
  const [inputMethod, setInputMethod] = useState<'text' | 'file'>(externalSequence ? 'text' : 'file');
  const [internalSequence, setInternalSequence] = useState('');
  const [internalFile, setInternalFile] = useState<File | null>(null);
  const [errorMessage, setErrorMessage] = useState('');
  
  // 
  const [substrateInputMethod, setSubstrateInputMethod] = useState<'text' | 'file'>('text');
  const [substrateSmiles, setSubstrateSmiles] = useState('');
  const [substrateFile, setSubstrateFile] = useState<File | null>(null);

  // 
  const sequence = externalSequence !== undefined ? externalSequence : internalSequence;
  const file = externalFile !== undefined ? externalFile : internalFile;

  // 
  const defaultPlaceholder = sequenceType === 'protein' 
    ? t('components.sequenceInput.proteinPlaceholder', ' (FASTA)') 
    : t('components.sequenceInput.dnaPlaceholder', 'DNA/RNA (FASTA)');

  const handleSequenceChange = (e: ChangeEvent<HTMLTextAreaElement>) => {
    const newValue = e.target.value;
    if (externalSequenceChange) {
      externalSequenceChange(newValue);
    } else {
      setInternalSequence(newValue);
    }
    setErrorMessage('');
  };

  const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const newFile = e.target.files[0];
      if (externalFileChange) {
        externalFileChange(newFile);
      } else {
        setInternalFile(newFile);
      }
      setErrorMessage('');
    }
  };

  const handleSubstrateChange = (e: ChangeEvent<HTMLInputElement>) => {
    setSubstrateSmiles(e.target.value);
    setErrorMessage('');
  };

  const handleSubstrateFileChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      setSubstrateFile(e.target.files[0]);
      setErrorMessage('');
    }
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!onSubmit) return;
    
    // 
    if (inputMethod === 'text' && sequence.trim() === '') {
      setErrorMessage(t('components.sequenceInput.errors.emptySequence', `${sequenceType === 'protein' ? '' : 'DNA/RNA'}`));
      return;
    }
    
    if (inputMethod === 'file' && !file) {
      setErrorMessage(t('components.sequenceInput.errors.noFile', ''));
      return;
    }
    
    if (includeSubstrate) {
      if (substrateInputMethod === 'text' && substrateSmiles.trim() === '') {
        setErrorMessage(t('components.sequenceInput.errors.emptySmiles', 'SMILES'));
        return;
      }
      
      if (substrateInputMethod === 'file' && !substrateFile) {
        setErrorMessage(t('components.sequenceInput.errors.noSmilesFile', 'SMILES'));
        return;
      }
    }
    
    // 
    onSubmit(inputMethod === 'text' ? sequence : '', file);
  };

  // 
  const fileExtensions = sequenceType === 'protein' 
    ? ".fasta,.fa,.txt" 
    : ".fasta,.fa,.gb,.txt";

  return (
    <div>
      {onSubmit ? (
        <Form onSubmit={handleSubmit}>
          {errorMessage && <Alert variant="danger">{errorMessage}</Alert>}
          
          <Form.Group className="mb-4">
            <Form.Label>
              {sequenceType === 'protein' 
                ? t('components.sequenceInput.proteinInputMethod', '')
                : t('components.sequenceInput.dnaInputMethod', 'DNA/RNA')}
            </Form.Label>
            <div className="d-flex gap-3 mb-3">
              <Form.Check
                type="radio"
                id="sequence-text"
                label={t('components.sequenceInput.directInput', '')}
                checked={inputMethod === 'text'}
                onChange={() => setInputMethod('text')}
              />
              <Form.Check
                type="radio"
                id="sequence-file"
                label={t('components.sequenceInput.uploadFile', '')}
                checked={inputMethod === 'file'}
                onChange={() => setInputMethod('file')}
              />
            </div>
            
            {inputMethod === 'text' ? (
              <Form.Control
                as="textarea"
                rows={5}
                placeholder={placeholder || defaultPlaceholder}
                value={sequence}
                onChange={handleSequenceChange}
                className="sequence-input"
              />
            ) : (
              <Form.Control
                type="file"
                accept={fileExtensions}
                onChange={handleFileChange}
              />
            )}
            <Form.Text className="text-muted">
              {t('components.sequenceInput.formatSupport', 'FASTA')}
            </Form.Text>
          </Form.Group>
          
          {includeSubstrate && (
            <Form.Group className="mb-4">
              <Form.Label>{t('components.sequenceInput.substrateInputMethod', '')}</Form.Label>
              <div className="d-flex gap-3 mb-3">
                <Form.Check
                  type="radio"
                  id="substrate-text"
                  label={t('components.sequenceInput.directSmilesInput', 'SMILES')}
                  checked={substrateInputMethod === 'text'}
                  onChange={() => setSubstrateInputMethod('text')}
                />
                <Form.Check
                  type="radio"
                  id="substrate-file"
                  label={t('components.sequenceInput.uploadSmilesFile', 'SMILES')}
                  checked={substrateInputMethod === 'file'}
                  onChange={() => setSubstrateInputMethod('file')}
                />
              </div>
              
              {substrateInputMethod === 'text' ? (
                <Form.Control
                  type="text"
                  placeholder={t('components.sequenceInput.smilesPlaceholder', 'SMILES')}
                  value={substrateSmiles}
                  onChange={handleSubstrateChange}
                />
              ) : (
                <Form.Control
                  type="file"
                  accept=".smi,.txt"
                  onChange={handleSubstrateFileChange}
                />
              )}
              <Form.Text className="text-muted">
                {t('components.sequenceInput.smilesSupport', 'SMILES')}
              </Form.Text>
            </Form.Group>
          )}
          
          <div className="d-grid">
            <Button 
              variant="primary" 
              type="submit" 
              size="lg" 
              disabled={isLoading}
            >
              {isLoading 
                ? t('components.sequenceInput.predicting', '...') 
                : t('components.sequenceInput.startPredict', '')}
            </Button>
          </div>
        </Form>
      ) : (
        <div>
          <Form.Group className="mb-4">
            {inputMethod === 'text' ? (
              <Form.Control
                as="textarea"
                rows={5}
                placeholder={placeholder || defaultPlaceholder}
                value={sequence}
                onChange={handleSequenceChange}
                className="sequence-input"
              />
            ) : (
              <Form.Control
                type="file"
                accept={fileExtensions}
                onChange={handleFileChange}
              />
            )}
            <div className="d-flex gap-3 mt-2">
              <Form.Check
                type="radio"
                id="sequence-text"
                label={t('components.sequenceInput.directInput', '')}
                checked={inputMethod === 'text'}
                onChange={() => setInputMethod('text')}
              />
              <Form.Check
                type="radio"
                id="sequence-file"
                label={t('components.sequenceInput.uploadFile', '')}
                checked={inputMethod === 'file'}
                onChange={() => setInputMethod('file')}
              />
            </div>
          </Form.Group>
        </div>
      )}
    </div>
  );
};

export default SequenceInput; 