import React from 'react';
import { Container, Row, Col, Card, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const Training: React.FC = () => {
  const { t } = useTranslation();
  
  // 
  const trainingServices = [
    {
      id: 'kcat',
      title: t('training.services.kcat.title', 'Kcat'),
      description: t('training.services.kcat.description', '-Kcat'),
      icon: '',
      route: '/training/kcat'
    },
    {
      id: 'km',
      title: t('training.services.km.title', 'Km'),
      description: t('training.services.km.description', '-Km'),
      icon: '',
      route: '/training/km'
    },
    {
      id: 'solubility',
      title: t('training.services.solubility.title', ''),
      description: t('training.services.solubility.description', ''),
      icon: '',
      route: '/training/solubility'
    },
    {
      id: 'promoter',
      title: t('training.services.promoter.title', ''),
      description: t('training.services.promoter.description', ''),
      icon: '',
      route: '/training/promoter'
    },
    {
      id: 'rbs',
      title: t('training.services.rbs.title', 'RBS'),
      description: t('training.services.rbs.description', 'RBSRBS'),
      icon: '',
      route: '/training/rbs'
    }
  ];

  return (
    <Container>
      <h1 className="mb-4">{t('training.pageTitle', '')}</h1>
      
      <Row className="mb-4">
        <Col>
          <Card className="shadow-sm">
            <Card.Body>
              <h5>{t('training.customTitle', '')}</h5>
              <p>
                {t('training.customDescription', '')}
              </p>
              <p>
                <strong>{t('training.advantages', '')}</strong>
              </p>
              <ul>
                <li>{t('training.advantagesList.ownData', '')}</li>
                <li>{t('training.advantagesList.customization', '')}</li>
                <li>{t('training.advantagesList.accuracy', '')}</li>
                <li>{t('training.advantagesList.download', '')}</li>
              </ul>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <h2 className="mb-3">{t('training.availableServices', '')}</h2>
      <Row xs={1} md={2} className="g-4 mb-5">
        {trainingServices.map((service) => (
          <Col key={service.id}>
            <Card className="h-100 shadow-sm hover-card">
              <Card.Body>
                <div className="d-flex align-items-center mb-3">
                  <div className="service-icon me-3">{service.icon}</div>
                  <h5 className="mb-0">{service.title}</h5>
                </div>
                <Card.Text>{service.description}</Card.Text>
                <div className="d-grid gap-2">
                  <Link to={service.route}>
                    <Button 
                      variant="outline-primary"
                      className="w-100"
                    >
                      {t('training.startTraining', '')}
                    </Button>
                  </Link>
                </div>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>

      <Row className="mb-4">
        <Col>
          <Card className="bg-light">
            <Card.Body>
              <h5>{t('training.howToUse', '')}</h5>
              <ol>
                <li>{t('training.steps.selectModel', '')}</li>
                <li>{t('training.steps.uploadData', 'CSVExcel')}</li>
                <li>{t('training.steps.setParams', '')}</li>
                <li>{t('training.steps.submitJob', '')}</li>
                <li>{t('training.steps.waitCompletion', '')}</li>
                <li>{t('training.steps.download', '')}</li>
              </ol>
              <p className="mb-0">
                <strong>{t('training.note', '')}</strong> {t('training.trainingTimeNote', '')}
              </p>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card className="shadow-sm mb-4">
            <Card.Body>
              <h5>{t('training.viewTasks', '')}</h5>
              <p>
                {t('training.viewTasksDescription', '')}
              </p>
              <Link to="/training/jobs">
                <Button 
                  variant="primary"
                >
                  {t('training.viewMyTasks', '')}
                </Button>
              </Link>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default Training; 